:root {
  //navigation
  --navigation-size: auto;
  --navigation-position: left;
  --navigation-font-transform: unset;
  --navigation-background-color: #3f3a3a;
  --navigation-border-width: 1px;
  --navigation-border-color: #393434;
  --navigation-separator-width: 0px;
  --navigation-separator-color: #090909;
  --navigation-active-style: left-border;
  --navigation-active-font-color: #676a6c;
  --navigation-active-background-color: #f39536;
  --navigation-subnav-font-color: #f7f7f7;
  --navigation-subnav-background-color: #3c3737;
  --navigation-client-font-family: var(--font-family);
  --navigation-client-font-size: 1rem;
  --navigation-client-font-color: #f7f7f7;
  --navigation-client-background-color: #3d3939;
  --navigation-client-border-width: 1px;
  --navigation-client-border-color: #373333;
  --navigation-logo-background-color: #000000;
  --navigation-logo-border-width: 1px;
  --navigation-logo-border-color: #000000;
  --top-navigation-style: hidden;
  --top-navigation-background-color: #3f3a3a;
  --top-navigation-border-width: 1px;
  --top-navigation-border-color: #393434;
  --top-navigation-subnav-font-color: #f7f7f7;
  --top-navigation-subnav-background-color: #3c3737;
  --top-navigation-active-font-color: #000000;
  --top-navigation-active-background-color: transparent;
  --top-navigation-active-tab-font-color: #f7f7f7;
  --top-navigation-active-tab-background-color: #3c3737;
  --top-navigation-active-indicator-style: none;
  --top-navigation-active-indicator-color: #f39536;
  --top-navigation-logo-style: right;
  --p-tabs-tab-color: var(--navigation-client-font-color) !important;
}
