@use 'styles/variables' as *;
@use 'styles/components' as *;
@use 'styles/modules' as *;
@use 'styles/utilities' as *;

@import url('https://fonts.googleapis.com/css2?family=Ubuntu:ital,wght@0,300;0,400;0,500;0,700;1,300;1,400;1,500;1,700&display=swap');
@import 'primeicons/primeicons.css';
@import '@material-design-icons/font/outlined.css';
// Tailwind CSS layers
@layer tailwind-base, primeng, tailwind-utilities;

@layer tailwind-base {
  @tailwind base;
}

@layer tailwind-utilities {
  @tailwind components;
  @tailwind utilities;
}

// Apply global styles
html,
body {
  font-family: var(--font-family);
  font-size: var(--base-font-size);
  font-weight: var(--font-weight);
  background-color: var(--main-background-color);
  color: var(--nav-text-color);
  height: 100vh;
  height: 100dvh;
  height: calc(var(--vh, 1vh) * 100);
  overflow: hidden;
}

/* CSS custom properties for viewport height */
:root {
  --vh: 1vh;
}

html {
  --vh: 1vh;
}

.scrollbars-visible ::-webkit-scrollbar {
  width: 12px;
  height: 12px;
}

::-webkit-scrollbar-button {
  display: none;
}

// track
::-webkit-scrollbar-corner,
::-webkit-resizer,
::-webkit-scrollbar-track {
  background-color: var(--scrollbar-track-color);
}

// handle
::-webkit-scrollbar-thumb {
  background: var(--scrollbar-handle-color);
  border-style: solid;
  border-color: transparent;
  border-width: var-if(var(--scrollbar-handle-padded), 3px, 0px);
  border-radius: var-if(var(--enable-rounded-corners), 20px, 0px);
  background-clip: padding-box;
}

* {
  // Firefox
  scrollbar-color: var(--scrollbar-handle-color) var(--scrollbar-track-color);
  scrollbar-width: thin;
}

.custom-icon-svg {
  fill: currentColor;
  transition: fill 0.2s ease;
}

a {
  cursor: pointer;
  color: var(--color-primary-color);
}

h1,
h2,
h3,
h4,
h5,
h6 {
  color: var(--header-font-color);
}

.card-border {
  border-color: var(--input-border-color) !important;
  border-width: var(--input-border-width) !important;
}

.underlined-field {
  color: var(--color-primary-color);
  font-weight: 500;
  padding: 0.25rem 0;

  &:hover {
    text-decoration: underline;
  }

  &.disabled-text {
    color: #ccc;
    cursor: not-allowed;
    &:hover {
      color: #ccc;
      text-decoration: underline;
    }
  }
}

.highlight-match {
  color: var(--p-primary-color);
  font-weight: 600;
  background-color: transparent;
}
