:root {
  // Button
  --button-font-family: var(--font-family);
  --button-font-size: 1rem;
  --button-font-color: #f7f7f7;
  --button-font-weight: 400;
  --button-font-transform: unset;
  --button-border-radius: 2px;

  // Input
  --input-font-family: var(--font-family);
  --input-font-size: 1rem;
  --input-font-color: #f7f7f7;
  --input-font-weight: 400;

  //modal
  --modal-header-font-family: var(--font-family);
  --modal-header-font-size: 1rem;
  --modal-header-font-color: #f7f7f7;
  --modal-header-font-weight: 400;
  --modal-header-font-transform: unset;
  --modal-footer-font-family: var(--font-family);
  --modal-footer-font-size: 1rem;
  --modal-footer-font-color: #f7f7f7;

  // Tab
  --tab-font-family: var(--font-family);
  --tab-font-size: 1rem;
  --tab-font-color: #f7f7f7;
  --tab-font-weight: 400;
  --tab-font-transform: unset;
  --tab-active-font-color: #f7f7f7;
  --tab-active-indicator-style: none;
  --tab-active-indicator-color: #f39536;

  // Accordion
  --accordion-font-family: var(--font-family);
  --accordion-font-color: #f7f7f7;
  --accordion-font-size: 0.9rem;
  --accordion-font-weight: 500;
  --accordion-font-transform: uppercase;
  --accordion-active-font-color: #f7f7f7;
  --p-accordion-header-color: var(--accordion-font-color) !important;
  --p-accordion-header-active-hover-color: var(
    --p-accordion-header-active-color
  );

  // Table
  --table-font-family: var(--font-family);
  --table-font-size: 1rem;
  --table-font-color: #f7f7f7;
  --table-font-weight: 400;
  --table-header-font-family: var(--font-family);
  --table-header-font-size: 1rem;
  --table-header-font-color: #f7f7f7;
  --table-header-font-weight: 500;
  --table-header-font-transform: unset;
  --table-active-font-color: #676a6c;

  // Tree
  --tree-active-font-color: #676a6c;
  --p-tree-node-icon-color: var(--main-font-color) !important;
  --p-tree-node-toggle-button-color: var(--main-font-color) !important;
  --p-tree-node-selected-background: var(
    --tree-active-background-color
  ) !important;
  --p-tree-node-selected-color: var(--tree-active-font-color) !important;
  --p-tree-node-icon-selected-color: var(--tree-active-font-color) !important;
  --p-tree-node-padding: calc(0.5 * var(--padding)) !important;

  // List
  --list-font-family: var(--font-family);
  --list-font-size: 1rem;
  --list-font-color: #f7f7f7;
  --list-font-weight: 400;
  --list-icon-color: #393434;
  --list-active-font-color: #f7f7f7;
  --list-active-icon-color: #f39536;

  // Feed
  --feed-font-family: var(--font-family);
  --feed-font-size: 1rem;
  --feed-font-color: #f7f7f7;
  --feed-font-weight: 400;
  --feed-header-font-color: #f7f7f7;
  --feed-active-font-color: #f7f7f7;

  // Graph
  --graph-palette: default;
  --graph-label-font-family: var(--font-family);
  --graph-label-font-size: 0.8rem;
  --graph-label-font-color: #ffffff;
  --graph-label-font-weight: 400;

  // Loader
  --loader-style: spinner;
  --loader-font-color: #f7f7f7;

  // Sidebar
  --sidebar-font-family: var(--font-family);
  --sidebar-font-size: 1rem;
  --sidebar-font-color: #f7f7f7;
  --sidebar-background-color: #3f3a3a;
  --sidebar-border-width: 1px;
  --sidebar-border-color: #3c3737;
  --sidebar-shadow: none;

  // Snackbar
  --snackbar-font-family: var(--font-family);
  --snackbar-font-size: 1rem;
  --snackbar-font-color: #f7f7f7;
  --snackbar-font-weight: 400;

  // Map Control
  --map-control-font-family: var(--font-family);
  --map-control-font-size: 1rem;
  --map-control-font-color: #676a6c;
  --map-control-font-weight: 400;

  // Main
  --main-font-family: var(--font-family);
  --main-font-size: 1rem;
  --main-font-weight: 400;
  --main-font-color: #f7f7f7;

  // Popover
  --p-popover-background: var(--input-background-color) !important;
}
