:root {
  //header
  --header-background-color: #3f3a3a;
  --header-border-color: #3c3737;
  --header-border-width: 1px;
  --header-font-color: #f7f7f7;
  --header-font-family: var(--font-family);
  --header-font-size: 1rem;
  --header-font-transform: unset;
  --header-font-weight: 400;
  --subheader-background-color: #3d3939;
  --subheader-border-color: #3a3636;
  --subheader-font-color: #f7f7f7;
  --subheader-font-family: var(--font-family);
  --subheader-font-size: 1rem;
  --subheader-font-transform: unset;
  --subheader-font-weight: 400;
}
