.p-paginator {
  border: var(--width-border) solid var(--footer-border-color) !important;
  background: var(--footer-background-color) !important;

  .p-paginator-page-selected {
    color: var(--color-primary-contrast) !important;
    background-color: var(--color-primary-color) !important;
    border-color: var(--color-primary-color) !important;
    box-shadow: var(--button-active-border-shadow);
    opacity: 0.9;
  }
}
