:root {
  // Typography
  --font-family: 'Ubuntu', serif;
  --base-font-size: 13px;
  --font-weight: 400;
  --content-font-family: var(--font-family);
  --content-font-size: 1rem;
  --content-font-color: #f7f7f7;
  --navigation-font-family: var(--font-family);
  --navigation-font-size: 1rem;
  --navigation-font-color: #f7f7f7;
  --navigation-font-weight: 400;
  --top-navigation-font-family: var(--font-family);
  --top-navigation-font-size: 1rem;
  --top-navigation-font-color: #f7f7f7;
  --top-navigation-font-weight: 400;
  --header-font-family: var(--font-family);
  --header-font-size: 1rem;
  --header-font-color: #f7f7f7;
  --header-font-weight: 400;
  --footer-font-family: var(--font-family);
  --footer-font-size: 1rem;
  --footer-font-color: #f7f7f7;
  --modal-font-family: var(--font-family);
  --modal-font-size: 1rem;
  --modal-font-color: #f7f7f7;
  --button-font-family: var(--font-family);
  --button-font-size: 1rem;
  --button-font-color: #f7f7f7;
  --button-font-weight: 400;
  --input-font-family: var(--font-family);
  --input-font-size: 1rem;
  --input-font-color: #f7f7f7;
  --input-font-weight: 400;
  --tab-font-family: var(--font-family);
  --tab-font-size: 1rem;
  --tab-font-color: #f7f7f7;
  --tab-font-weight: 400;
  --accordion-font-family: var(--font-family);
  --accordion-font-color: #f7f7f7;
  --accordion-font-size: 0.9rem;
  --accordion-font-weight: 500;
  --table-font-family: var(--font-family);
  --table-font-size: 1rem;
  --table-font-color: #f7f7f7;
  --table-font-weight: 400;
  --list-font-family: var(--font-family);
  --list-font-size: 1rem;
  --list-font-color: #f7f7f7;
  --list-font-weight: 400;
  --feed-font-family: var(--font-family);
  --feed-font-size: 1rem;
  --feed-font-color: #f7f7f7;
  --feed-font-weight: 400;
  --graph-label-font-family: var(--font-family);
  --graph-label-font-size: 0.8rem;
  --graph-label-font-color: #ffffff;
  --graph-label-font-weight: 400;
  --sidebar-font-family: var(--font-family);
  --sidebar-font-size: 1rem;
  --sidebar-font-color: #f7f7f7;
  --snackbar-font-family: var(--font-family);
  --snackbar-font-size: 1rem;
  --snackbar-font-color: #f7f7f7;
  --snackbar-font-weight: 400;
  --map-control-font-family: var(--font-family);
  --map-control-font-size: 1rem;
  --map-control-font-color: #676a6c;
  --map-control-font-weight: 400;
  --main-gutter-width: 0;
}
