:root {
  // Background Colors
  --accordion-active-background-color: #3d3939;
  --accordion-background-color: #3f3a3a;
  --button-background-color: #3f3a3a;
  --content-background-color: #3f3a3a;
  --feed-active-background-color: #3d3939;
  --feed-background-color: #3f3a3a;
  --feed-header-background-color: #3f3a3a;
  --footer-background-color: #3d3939;
  --graph-background-color: #3f3a3a;
  --header-background-color: #3f3a3a;
  --input-background-color: #454040;
  --list-active-background-color: #3d3939;
  --list-background-color: #3f3a3a;
  --loader-overlay-background-color: rgba(10, 10, 10, 0.4);
  --main-background-color: #0a0a0a;
  --map-control-background-color: #ffffff;
  --map-control-secondary-background-color: #f2f2f2;
  --modal-background-color: #3f3a3a;
  --modal-footer-background-color: #3d3939;
  --modal-header-background-color: #3f3a3a;
  --nav-logo-bg-color: #000000;
  --sidebar-background-color: #3f3a3a;
  --snackbar-background-color: #4c4646;
  --tab-active-background-color: #3c3737;
  --tab-background-color: #3f3a3a;
  --table-active-background-color: #f39536;
  --table-background-color: #3f3a3a;
  --table-header-background-color: #3d3939;
  --tree-active-background-color: #f39536;
  --tree-background-color: #3f3a3a;

  // Border Colors
  --accordion-active-border-color: #373333;
  --accordion-border-color: #3c3737;
  --content-border-color: #393434;
  --feed-border-color: #3c3737;
  --feed-header-border-color: #3c3737;
  --footer-border-color: #373333;
  --input-border-color: #393434;
  --list-border-color: #3c3737;
  --main-border-color: #090909;
  --map-control-border-color: #e6e6e6;
  --modal-border-color: #393434;
  --modal-footer-border-color: #373333;
  --modal-header-border-color: #3c3737;
  --sidebar-border-color: #3c3737;
  --snackbar-border-color: #443f3f;
  --tab-border-color: #3c3737;
  --table-active-border-color: #f1861a;
  --table-border-color: #3c3737;
  --table-header-border-color: #373333;
  --tree-border-color: #393434;

  // Shadows
  --button-active-border-shadow: none;
  --button-border-shadow: none;
  --button-hover-border-shadow: none;
  --content-shadow: none;
  --main-shadow: none;
  --sidebar-shadow: none;

  // Border Widths
  --accordion-active-border-width: 1px;
  --accordion-border-width: 1px;
  --content-border-width: 1px;
  --feed-border-width: 1px;
  --footer-border-width: 1px;
  --input-border-width: 1px;
  --list-border-width: 1px;
  --map-control-border-width: 1px;
  --modal-border-width: 1px;
  --modal-footer-border-width: 1px;
  --modal-header-border-width: 1px;
  --sidebar-border-width: 1px;
  --snackbar-border-width: 1px;
  --tab-border-width: 1px;
  --table-active-border-width: 1px;
  --table-border-width: 1px;
  --table-header-border-width: 1px;
  --tree-border-width: 1px;

  --p-content-background: var(--content-background-color) !important;
  --p-content-border-color: var(--content-border-color);
  --p-content-color: var(--content-font-color) !important;
  --p-text-color: var(--main-font-color) !important;
}
