  --scrollbar-track-color: rgba(0, 0, 0, 0.01);
  --scrollbar-handle-color: rgba(0, 0, 0, 0.1);
  --scrollbar-handle-padded: 1;
  --color-click-animation: #999999;
}
:root .modal-backdrop {
  --opacity-modal-backdrop: 0.5;
}

.p-button {
  background-color: var(--color-primary-color) !important;
  border-color: var(--color-primary-color) !important;
  color: var(--color-primary-contrast) !important;
}

.p-button.p-button-secondary {
  background-color: var(--color-secondary-color) !important;
  border-color: var(--color-secondary-color) !important;
  color: var(--color-secondary-contrast) !important;
}

.p-button.p-button-success {
  background-color: var(--color-success-color) !important;
  border-color: var(--color-success-color) !important;
  color: var(--color-success-contrast) !important;
}

.p-button.p-button-info {
  background-color: var(--color-info-color) !important;
  border-color: var(--color-info-color) !important;
  color: var(--color-info-contrast) !important;
}

.p-button.p-button-help {
  background-color: var(--color-notify-color) !important;
  border-color: var(--color-notify-color) !important;
  color: var(--color-notify-contrast) !important;
}

.p-button.p-button-warn {
  background-color: var(--color-warning-color) !important;
  border-color: var(--color-warning-color) !important;
  color: var(--color-warning-contrast) !important;
}

.p-button.p-button-danger {
  background-color: var(--color-danger-color) !important;
  border-color: var(--color-danger-color) !important;
  color: var(--color-danger-contrast) !important;
}

:root {
  --p-inputtext-placeholder-color: var(
    --input-placeholder-color,
    rgba(var(--input-font-color), 0.6)
  ) !important;
  --p-form-field-color: var(--input-font-color) !important;
  --p-form-field-icon-color: var(--input-font-color) !important;
  --p-form-field-placeholder-color: var(--input-placeholder-color) !important;
  --p-form-field-background-color: var(--input-background-color) !important;
  --p-form-field-focus-border-color: var(--color-primary-color) !important;
  --p-overlay-select-background: var(--input-background-color) !important;
  --p-multiselect-background: var(--input-background-color) !important;
  --p-listbox-background: var(--input-background-color) !important;
}

input:not(.p-colorpicker-preview),
textarea,
p-select,
.p-treeselect,
.p-inputgroupaddon,
.p-autocomplete,
.p-autocomplete-input-multiple {
  background: var(--input-background-color) !important;
  border-color: var(--input-border-color) !important;
}
input:not(.p-colorpicker-preview):enabled:focus,
textarea:enabled:focus,
p-select:enabled:focus,
.p-treeselect:enabled:focus,
.p-inputgroupaddon:enabled:focus,
.p-autocomplete:enabled:focus,
.p-autocomplete-input-multiple:enabled:focus {
  border-color: var(--color-primary-color) !important;
}
input:not(.p-colorpicker-preview):disabled,
textarea:disabled,
p-select:disabled,
.p-treeselect:disabled,
.p-inputgroupaddon:disabled,
.p-autocomplete:disabled,
.p-autocomplete-input-multiple:disabled {
  opacity: 0.5 !important;
}

.p-disabled {
  background: var(--p-select-disabled-background) !important;
}

.p-treeselect {
  align-items: center;
}

.pill {
  background: var(--color-primary-color);
  color: white;
}

p-autocomplete {
  border-radius: var(--p-autocomplete-border-radius);
}

.p-dialog {
  background: var(--modal-background-color) !important;
  border-color: var(--modal-border-color) !important;
}

.p-dialog-title {
  font-family: var(--modal-header-font-family) !important;
  font-size: var(--modal-header-font-size) !important;
  font-weight: var(--modal-header-font-weight) !important;
  text-transform: var(--modal-header-font-transform);
}

.p-dialog-header {
  color: var(--modal-header-font-color) !important;
  border-bottom: var(--width-border) solid var(--modal-header-border-color);
}

.p-treetable,
.p-datatable {
  position: relative;
  background: var(--table-background-color);
  color: var(--table-font-color);
}
.p-treetable thead,
.p-datatable thead {
  background: var(--main-border-color);
}
.p-treetable thead > tr > th,
.p-datatable thead > tr > th {
  color: var(--table-header-font-color);
  background: var(--table-header-background-color);
  border-bottom: var(--width-border) solid var(--table-header-border-color);
  font-family: var(--table-header-font-family);
  font-size: var(--table-header-font-size);
  font-weight: var(--table-header-font-weight);
  text-transform: var(--table-header-font-transform);
}
.p-treetable thead > tr > th a,
.p-datatable thead > tr > th a {
  color: var(--table-header-font-color);
}
.p-treetable tbody > tr,
.p-datatable tbody > tr {
  font-family: var(--table-font-family);
  font-size: var(--table-font-size);
  font-weight: var(--table-font-weight);
  color: var(--table-font-color);
  background: var(--table-background-color);
}
.p-treetable tbody > tr > td,
.p-datatable tbody > tr > td {
  background: inherit;
}
.p-treetable .p-highlight i,
.p-datatable .p-highlight i {
  color: var(--table-active-font-color);
}

.table-bordered thead tr th,
.table-bordered thead tr td,
.table-bordered tbody tr th,
.table-bordered tbody tr td {
  border-style: solid;
  border-width: 0 var(--width-border) var(--width-border) 0;
}
.table-bordered thead tr th,
.table-bordered tbody tr th {
  border-color: var(--table-header-border-color);
}
.table-bordered thead tr td,
.table-bordered tbody tr td {
  border-color: var(--table-border-color);
}

.table-selectable .p-treetable-hoverable-rows tbody > tr:not(.p-highlight):hover,
.table-selectable .p-datatable-hoverable-rows tbody > tr:not(.p-highlight):hover {
  cursor: pointer;
  color: var(--table-active-font-color);
  background: var(--table-active-background-color);
}
.table-selectable .p-treetable tbody > tr.p-highlight,
.table-selectable .p-datatable tbody > tr.p-highlight {
  background: var(--color-primary-color);
  color: var(--color-primary-contrast);
}
.table-selectable .p-treetable tbody > tr.p-highlight > td,
.table-selectable .p-treetable tbody > tr.p-highlight > th,
.table-selectable .p-datatable tbody > tr.p-highlight > td,
.table-selectable .p-datatable tbody > tr.p-highlight > th {
  border-color: inherit;
}

.p-paginator {
  border: var(--width-border) solid var(--footer-border-color) !important;
  background: var(--footer-background-color) !important;
}
.p-paginator .p-paginator-page-selected {
  color: var(--color-primary-contrast) !important;
  background-color: var(--color-primary-color) !important;
  border-color: var(--color-primary-color) !important;
  box-shadow: var(--button-active-border-shadow);
  opacity: 0.9;
}

table.info-display td {
  vertical-align: top;
}
table.info-display td:first-child {
  white-space: nowrap;
  padding-right: 0.8rem;
  text-align: right;
  border-right: solid var(--width-border) rgba(0, 0, 0, 0.05);
  text-transform: uppercase;
  opacity: 0.6;
  font-size: 0.8125rem;
  padding-top: 3px;
  min-width: 80px;
}
table.info-display td:last-child {
  padding-left: 0.8rem;
  word-break: break-word;
}
table.info-display td:last-child img {
  max-width: 150px;
}
table.info-display td:last-child span.units {
  opacity: 0.6;
  font-size: 0.9rem;
  padding-left: 0.4rem;
}

.action {
  position: relative;
  display: inherit;
  cursor: pointer;
}
.action__container {
  display: none;
  position: absolute;
  width: 80px;
  height: 80px;
  top: 0;
  left: 0;
  pointer-events: none;
}
.action__pulse {
  width: 100%;
  height: 100%;
  background-color: var(--color-click-animation);
  border-radius: 100%;
  transform: scale(0);
  opacity: 1;
}
.action--animate .action__container {
  display: block;
}
.action--animate .action__pulse {
  display: block;
  animation: action-pulseScaleOut 0.6s 1;
}

@keyframes action-pulseScaleOut {
  0% {
