//
// disabled/muted class :: color agnostig
//
.disabled,
.disabled:hover,
.disabled:active {
  opacity: 0.6 !important;
  cursor: default;
}

.muted {
  opacity: 0.6 !important;
}

.muted-less {
  opacity: 0.8 !important;
}

.muted-more {
  opacity: 0.4 !important;
}

.icon-light {
  color: #ddd;
}

.icon-primary {
  color: var(--color-primary-color);
}

//
// text
//

.text-link {
  color: var(--color-primary-color);
  cursor: pointer;

  &:hover {
    opacity: 0.9;
    text-decoration: underline;
  }

  &.base-color {
    color: var(--main-font-color);

    &:hover {
      opacity: 0.9;
    }
  }
}

//
// visiblity hidden
//
.visibility-hidden {
  visibility: hidden;
  width: 0;
  height: 0;
  padding: 0;
  margin: 0;
  border: none;
  overflow: hidden;
  appearance: none;
}

.min-width-0 {
  min-width: 0;
}

.justify-right {
  justify-content: right;
}
